* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:visited,
a:hover {
  text-decoration: none;
  outline: none;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid #372E11;
  outline-offset: 3px;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  font-size: 16px;
  line-height: 1;
  font-family: "Raleway", sans-serif;
  color: #372E11;
  font-weight: 500;
  font-style: normal;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: "Raleway", sans-serif;
  outline: none;
}

button {
  background-color: transparent;
  cursor: pointer;
  font-weight: 500;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  overflow: hidden;
}

