.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  transition: box-shadow 0.3s ease, background-color 0.3s ease;
}
.header.active {
  background-color: #FFFBED;
  box-shadow: 0 0 10px 10px rgba(55, 46, 17, 0.3);
}
.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 126px;
  padding: 10px 0;
}
@media (max-width: 600px) {
  .header__body {
    min-height: 58px;
    padding: 8px 0;
  }
}
