    width: 48px;
    height: 42px;
  }
}
.header__logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 991px) {
  .header__menu {
    display: flex;
    flex-direction: column;
    position: fixed;
    height: 100%;
    width: 40%;
    top: 0;
    left: 0;
    transform: translateX(-100%);
    overflow-y: auto;
    background-color: #FFFBED;
    box-shadow: 0 5px 20px rgba(55, 46, 17, 0.4);
    z-index: 50;
    padding: 50px 20px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease;
  }
  .header__menu.active {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    transition: all 0.5s ease;
  }
}
@media (max-width: 767.9px) {
