.actions-header__burger {
  display: none;
}
@media (max-width: 991px) {
  .actions-header__burger {
    position: relative;
    display: block;
    width: 25.5px;
    height: 18.5px;
    cursor: pointer;
    z-index: 10;
  }
  .actions-header__burger span {
    display: block;
    position: absolute;
    top: calc(50% - 1px);
    left: 0;
    height: 2px;
    width: 19px;
    border-radius: 2px;
    background-color: #372E11;
    transition: transform 0.3s ease, background-color 0.3s ease;
  }
  .actions-header__burger span:nth-child(1) {
    width: 100%;
    top: 0;
  }
  .actions-header__burger span:last-child {
    width: 12px;
    top: auto;
    bottom: 0;
  }
  .actions-header__burger.active span {
    transform: scale(0);
  }
  .actions-header__burger.active span:first-child {
    transform: rotate(45deg);
    top: calc(50% - 1px);
  }
  .actions-header__burger.active span:last-child {
    width: 100%;
    transform: rotate(-45deg);
    bottom: calc(50% - 1px);
  }
}
@media (max-width: 600px) {
  .actions-header__burger {
    width: 19.5px;
    height: 14.5px;
  }
  .actions-header__burger span {
    width: 14.5px;
  }
  .actions-header__burger span:nth-child(1) {
    width: 100%;
    top: 0;
  }
  .actions-header__burger span:last-child {
    width: 9.5px;
  }
}
@media (any-hover: hover) {
  .actions-header__burger:hover span {
    background-color: #ec9006;
  }
}

