.body-form__checkbox {
  display: none;
}
.body-form__checkbox:checked + .body-form__tag::after {
  opacity: 1;
  visibility: visible;
}
.body-form__checkbox:checked + .body-form__tag::before {
  background-color: #0E493F;
}
.body-form__checkbox:focus-visible + .body-form__tag {
  outline: 2px solid #372E11;
  outline-offset: 3px;
}
.body-form__tag {
  display: block;
  position: relative;
  font-family: "PT Sans";
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  padding-left: 30px;
}
.body-form__tag::before {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  width: 20px;
  height: 20px;
  border: 2px solid #0E493F;
  transition: background-color 0.3 ease;
}
@media (max-width: 600px) {
  .body-form__tag::before {
    top: 0;
  }
}
.body-form__tag::after {
  content: url("../img/svg/mark.svg");
  position: absolute;
  top: 3px;
  left: 4px;
  width: 16px;
  height: 16px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3 ease, visibility 0.3s ease;
}
@media (max-width: 600px) {
  .body-form__tag::after {
    top: 1px;
  }
}

